.footer {
  padding: 50px 0;

  @media (max-width: $tablet-width) {
    padding: 27px 0;
  }
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: $laptop-width) {
    flex-direction: column;
  }
}

.footer__copyright {
  display: flex;
  gap: 20px;
  margin-right: 15px;
  font-size: 20px;
  line-height: 50px;
  list-style: none;
  transition: color 200ms ease 0s;

  &:hover,
  &:focus {
    color: var(--special);
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 15px;
    margin-right: 0;
    font-size: 20px;
    line-height: 24px;
  }
}

.footer__school-link {
  &:hover,
  &:focus {
    color: var(--special);
  }
  font-size: 20px;
  line-height: 50px;
  transition: color 200ms ease 0s;

  @media (max-width: $tablet-width) {
    margin-bottom: 30px;
    font-size: 20px;
    line-height: 24px;
  }
}
